<template>
    <div class="pos">
        <el-row>
            <el-col :span='7' class="pos-order" id="order-list">
                <el-tabs>
                    <el-tab-pane label="点餐">
                        <el-table border style="width:100%" :data="tableData">
                            <el-table-column prop="goodsName" label="商品名称"></el-table-column>
                            <el-table-column prop="count" label="数量" width="50"></el-table-column>
                            <el-table-column prop="price" label="金额" width="70"></el-table-column>
                            <el-table-column label="操作" width="100" fixed="right">
                                <template scope="scope">
                                    <el-button type="text" size="small" @click="delSingGoods(scope.row)">删除</el-button>
                                    <el-button type="text" size="small" @click="addOrderList(scope.row)">添加</el-button>
                                </template>
                            </el-table-column>
                        </el-table>
                        <div class="total">
                            <small>数量：</small> {{totalNum}} &nbsp; <small>金额 ：</small> {{totalMoney}}元
                        </div>
                        <div class="btn-list">
                            <el-button type="warning">挂单</el-button>
                            <el-button type="danger" @click="delAll">删除</el-button>
                            <el-button type="success" @click="checkout">结账</el-button>
                        </div>

                    </el-tab-pane>
                    <el-tab-pane label="挂单">
                        挂单
                    </el-tab-pane>
                    <el-tab-pane label="外卖">
                        外卖
                    </el-tab-pane>
                </el-tabs>
            </el-col>
            <el-col :span='17'>
                <div class="often-goods">
                    <div class="title">常用商品</div>
                    <div class="ofter_goods_list">
                        <ul>
                            <li v-for="goods in ofterGoods" @click="addOrderList(goods)">
                                <span>{{goods.goodsName}}</span>
                                <span class="o_price">￥{{goods.price}}元</span>
                            </li>
                        </ul>
                    </div>
                </div>
                <div class="goods-type">
                    <el-tabs>
                        <el-tab-pane label="汉堡">
                            <div>
                                <ul class="cookList">
                                    <li v-for="type0 in type0Goods" @click="addOrderList(type0)">
                                        <span class="foodImg"><img :src="type0.goodsImg" alt="" width='100%'/></span>
                                        <div style="float:left">
                                            <span class="foodsName">{{type0.goodsName}}</span>
                                            <span class="foodPrice">￥{{type0.price}}元</span>
                                        </div>
                                    </li>
                                </ul>
                            </div>
                        </el-tab-pane>
                        <el-tab-pane label="小食">
                            <div>
                                <ul class="cookList">
                                    <li v-for="type0 in type1Goods" @click="addOrderList(type0)">
                                        <span class="foodImg"><img :src="type0.goodsImg" alt="" width='100%'/></span>
                                        <div style="float:left">
                                            <span class="foodsName">{{type0.goodsName}}</span>
                                            <span class="foodPrice">￥{{type0.price}}元</span>
                                        </div>
                                    </li>
                                </ul>
                            </div>
                        </el-tab-pane>
                        <el-tab-pane label="饮料">
                            <div>
                                <ul class="cookList">
                                    <li v-for="type0 in type2Goods" @click="addOrderList(type0)">
                                        <span class="foodImg"><img :src="type0.goodsImg" alt="" width='100%'/></span>
                                        <div style="float:left">
                                            <span class="foodsName">{{type0.goodsName}}</span>
                                            <span class="foodPrice">￥{{type0.price}}元</span>
                                        </div>
                                    </li>
                                </ul>
                            </div>
                        </el-tab-pane>
                        <el-tab-pane label="套餐">
                            <div>
                                <ul class="cookList">
                                    <li v-for="type0 in type3Goods" @click="addOrderList(type0)">
                                        <span class="foodImg"><img :src="type0.goodsImg" alt="" width='100%'/></span>
                                        <div style="float:left">
                                            <span class="foodsName">{{type0.goodsName}}</span>
                                            <span class="foodPrice">￥{{type0.price}}元</span>
                                        </div>
                                    </li>
                                </ul>
                            </div>
                        </el-tab-pane>
                    </el-tabs>
                </div>
            </el-col>
        </el-row>
    </div>
</template>


<script>
import axios from 'axios'
export default {
   name:"Pos" ,
   data(){
       return {
           tableData:[
           ],
           ofterGoods:[
             
           ],
           type0Goods:[
               
           ],
           type1Goods:[
             
           ],
           type2Goods:[
              
           ],
           type3Goods:[
             
           ],
           totalNum:0,
           totalMoney:0
       }
   },
   created(){
     this.getGoodsList()
   },
   mounted(){
       var orderHeight=document.body.clientHeight;
       document.getElementById("order-list").style.height=orderHeight+'px'
   },
   methods:{
       getGoodsList(){
           axios.get('/api')
           .then((res)=>{
               console.log(res.data.data)
               this.ofterGoods=res.data.data.ofterGoods;
               this.type0Goods=res.data.data.type0Goods;
               this.type1Goods=res.data.data.type1Goods;
               this.type2Goods=res.data.data.type2Goods;
               this.type3Goods=res.data.data.type3Goods;
           })
           
       },
       addOrderList(goods){
        this.totalNum=0;
        this.totalMoney=0;
        var tableData=this.tableData;
        //    商品是否已经存在与订单列表中
        let isHave=false;
        for(let i= 0 ; i< tableData.length;i++){
            if(tableData[i].goodsId==goods.goodsId){
                isHave=true
            }
        }

        // 根据判断编写业务逻辑
        if(isHave){
            // 改变商品数量
            let arr=tableData.filter(a=>a.goodsId==goods.goodsId);
            arr[0].count++;
        }else{
            let newGoods={goodsId:goods.goodsId,goodsName:goods.goodsName,price:goods.price,count:1};
            this.tableData.push(newGoods)
        }
        this.getTotal();
       },
       delSingGoods(goods){
           this.tableData=this.tableData.filter(a=>a.goodsId!=goods.goodsId);
           this.getTotal()
       },
    //    汇总数量和金额
       getTotal(){
           this.totalNum=0;
           this.totalMoney=0;
           if(this.tableData){
               this.tableData.forEach((ele)=>{
                    this.totalNum+=ele.count;
                    this.totalMoney=this.totalMoney+(ele.price*ele.count);
                })
           }
       },
       delAll(){
           this.tableData=[];
           this.totalNum=0;
           this.totalMoney=0;
       },
    //    模拟结账
       checkout(){
           if(this.totalNum!=0){
               this.tableData=[];
               this.totalNum=0;
               this.totalMoney=0;
               this.$message({
                   message:"结账成功，感谢您为店里做出来一份力",
                   type:"success"
               })
           }else{
               this.$message.error('别点啦，现在没有单哦')
           }
       },
   }
}
</script>

<style scoped>
    .pos-order{
        border-right:1px solid #c0ccda;
        background: #f9fafc ;
    }
    .btn-list{
        margin-top: 10px;
        text-align:center;
    }
    .often-goods .title{
        height: 20px;
        border-bottom: 1px solid #E4E7ED;
        background: #f9fafc;
        padding: 10px;
    }
    .ofter_goods_list ul li{
        float: left;
        border: 1px solid #e5e9f2;
        padding: 10px;
        background: #fff;
        margin: 10px;
        border-radius: 10px;
        cursor: pointer;
    }
    .o_price{
        color: #58b7ff
    }
    .goods-type{
        clear: both;
    }
    .cookList{
        padding: 20px;
    }
    .cookList li{
        border: 1px solid #E5E9F2;
        width: 23%;
        padding: 2px;
        float: left;
        margin: 2px;
        height: auto;
        background: #fff;
        cursor: pointer
    }
    .cookList li span{
        display: block;
   }
    .cookList .foodImg{
        width: 40%;
        float: left;
    }
     .cookList li .foodsName{
       font-size: 16px;
       padding-left: 10px;
       color:brown;
       /* float:none; */
   }

   .foodPrice{
       font-size: 16px;
       padding-left: 10px;
       padding-top:10px;
   }
   .total{
       text-align: left;
       background: #fff;
       padding: 10px;
       border-bottom: 1px solid #eeeeee
   }
</style>





